							<span class="ui-input-block">
								<input type="text" id="sender" name="sender" class="ui-input" value="" maxlength="30" placeholder="주문자 이름을 입력해주세요" v-model="order.name">
							</span>
						</dd>
						<dd class="form-style-list-val">
							<span class="ui-input-block">
								<input type="tel" id="maskSendmo" name="maskSendmo" class="ui-input" value="" maxlength="13" placeholder="주문자 휴대폰번호를 -없이 입력해주세요"  v-model="order.phone">
