		</div>
	</div>
</template>


<script>
import  ShopFooter from '@/components/common/ShopFooter'
import myMixin from '@/mixin/mixin'
import { mapGetters } from 'vuex'
export default {
    
	name: 'Shop',  
  mixins: [myMixin], 
  components: { 
		ShopFooter,
	},
	data() {
		return {
            receive:{
                name:'',
                phone:null,
                ribbonName1:null,
                addr:null,
                ribbonName2:'삼가 故人의 冥福을 빕니다'
            },
			order: {
				name: null,
				phone: null
			},
			nameSelfFlag: false,
            id:this.$route.query.id ? this.$route.query.id : null,
            EcodeId:this.$route.query.EcodeId ? this.$route.query.EcodeId : null,
			url: `${process.env.VUE_APP_BASEURL}/api/pay/payPage`,

		}
    },
    mounted(){
        
        const parameter2 ={
            EcodeId:this.EcodeId
        }
        this.$store.dispatch('REQUEST_OBITUARY_SHOP_INFO',parameter2)
        const parameter ={
            id:this.id,
            EcodeId:this.EcodeId
        }
		this.$store.dispatch('REQUEST_SHOP_DETAIL',parameter)
    },
	computed:{
        ...mapGetters(['getShopDetail','getShopObituaryDetail']),
        makeAddr(){
			
            if(this.getShopObituaryDetail.obituaryMember!==undefined && this.getShopObituaryDetail.obituaryMember.memberAttr.codeId==='TYPE-0'){
                return this.getShopObituaryDetail.obituaryMember.memberAddr + '  ' + this.getShopObituaryDetail.obituaryMember.memberPlaceName
            }else  if(this.getShopObituaryDetail.obituaryMember!==undefined && this.getShopObituaryDetail.obituaryMember.memberAttr.codeId==='TYPE-1'){
                return this.getShopObituaryDetail.obituaryAddr + '  ' + this.getShopObituaryDetail.obituaryAddrName
            }
            return ''
        },
        makeMortuary(){
			
            if(this.getShopObituaryDetail.obituaryMember!==undefined && this.getShopObituaryDetail.obituaryMember.memberAttr.codeId==='TYPE-0'){
				if(this.getShopObituaryDetail.obituaryMortuary!==undefined && this.getShopObituaryDetail.obituaryMortuary!==null){
					return this.getShopObituaryDetail.obituaryMortuary.mortuaryNumber
				}
            }else  if(this.getShopObituaryDetail.obituaryMember!==undefined && this.getShopObituaryDetail.obituaryMember.memberAttr.codeId==='TYPE-1'){
				
				if(this.getShopObituaryDetail.obituaryMortuaryName!==undefined && this.getShopObituaryDetail.obituaryMortuaryName!==null){
					return this.getShopObituaryDetail.obituaryMortuaryName
				}
				
			}
			
            return ''
        }

    },
    methods:{
		computedDelivery(shop) {
			return shop.deliveryCost
        },
		computedPrice2(shop) {
			return shop.shopPrice * (1 - shop.shopDc / 100)
        },
        computedPriceAll(shop){
            return this.computedDelivery(shop) + this.computedPrice2(shop)
        },
		receiveNameSelect(event){
			
			if(event.target.value === 'self'){
				this.receive.name = ''
				this.nameSelfFlag = true
			}else{
				this.nameSelfFlag = false
				this.receive.name = event.target.value
				for(let i=0;i<this.getShopObituaryDetail.obituaryObituaryMember.length;i++){
					if(this.getShopObituaryDetail.obituaryObituaryMember[i].obituaryMemberName === event.target.value){
						this.receive.phone = this.getShopObituaryDetail.obituaryObituaryMember[i].obituaryMemberPhone
					}
				}
			}

		},
		invalidationCheck(){
			if(this.receive.ribbonName1 == null || this.receive.ribbonName1 == undefined || this.receive.ribbonName1 === ''){
				alert('리본문구를 입력해 주세요.')
				return false;
			}
			
			if(this.order.phone == null || this.order.phone == undefined || this.order.phone === ''){
				alert('주문자 전화번호를 입력하세요.')
				return false;
			}
			
			return true;
		},
        requestPay(){
			if(!this.invalidationCheck()){
				return;
			}
			
			this.receive.addr = this.makeAddr + " " + this.makeMortuary
			this.receive.obituaryName = this.getShopObituaryDetail.obituaryName

            const itemList=
                [{
                    basketProductCount: 1,
                    basketProductType: 'SHOP',
                    basketMember:null,
                    basketShop: {id:this.getShopDetail.id}
                    }
                ]

            //PG결제
            const parameter = {
                purchaseType: 'SHOP-12-0',
				requestBody: {
                    // 2020-07-19, 배송비 추가 필요
                    allDeliveryPrice: this.computedDelivery(this.getShopDetail),
                    usingSafeNumber: 'N',
                    deliveryMemoTxt:'',
                    usingMemberPoint: 0,
                    totalSavings:0,
                    purchaseItemList: itemList,
                    purchaseAddressSave:false,// 기존 배송지로할지 안할지
                    purchaseReceiveAddress: JSON.stringify(this.receive), //받는사람 주소 json
                    purchaseOrderAddress: JSON.stringify(this.order), //보내는 사람 주소 json
                }
            }

			// 2020-07-19, PG 결제 모듈 사용
			const context = this
			const item = {
				price: this.computedPriceAll(this.getShopDetail),
				subject: this.getShopDetail.shopTitle,
				userId: null,
				userName: this.order.name,
				userPhone: this.order.phone,
				userAddr: this.order.addr,
				parameter: JSON.stringify(parameter)
            }

            	
			var paySubject = encodeURIComponent(item.subject)
			var payUserId = encodeURIComponent(item.userId)
			var payUserName = encodeURIComponent(item.userName)
			var payUserPhone = encodeURIComponent(item.userPhone)
			var payUserAddr = encodeURIComponent(item.userAddr)
			var payParameter = encodeURIComponent(item.parameter)

            
            window.location.href =  this.url + `?price=${item.price}&subject=${paySubject}&userId=${payUserId}&userName=${payUserName}&userPhone=${payUserPhone}&userAddr=${payUserAddr}&parameter=${payParameter}`
        },
        
        
    }
    
}
</script>